import { Box } from "@mui/material";
import { ReactNode } from "react";

const Loader = ({
	children,
	classes,
	contentClasses,
}: {
	children: ReactNode;
	classes?: string[];
	contentClasses?: string[];
}) => {
	return (
		<Box className={`site ${classes?.join(" ")}`}>
			<main className={`site__content ${contentClasses?.join(" ")}`}>{children}</main>
		</Box>
	);
};

export default Loader;
