import React from "react";
import { Page404 } from "../pages";
import { Home } from "../pages/Home";
import { Login } from "../pages/Login";

type RouteType = {
	path: string,
	element: React.ReactNode,
	name?: string
}

const routes : RouteType[] = [
	{
		path: '/',
		element: <Home />,
		name: 'Home'
	},
	{
		path: '/login',
		element: <Login />,
		name: 'Login'
	},
	{
		path: '*',
		element: <Page404 />,
		name: '404'
	}
]
export default routes;