import { useEffect, useMemo, useState } from "react";
import { padNumber } from "../../utils/helpers";

type CountDownProps = {
	availableTill: number;
	text?: string;
	hideDay?: boolean;
	hideHour?: boolean;
	hideMinute?: boolean;
	onSuccess?: Function;
};

const CountDown = ({ availableTill, text, hideDay, hideHour, hideMinute, onSuccess }: CountDownProps) => {
	const initState = useMemo(() => ({
		day: "00",
		hour: "00",
		minute: "00",
		second: "00",
	}), []);

	const [clock, setClock] = useState(initState);

	useEffect(() => {
		const days = 24* 60 * 60 * 1000;
		const hours = 60 * 60 * 1000;
		const minutes = 60 * 1000;
		const seconds = 1000;

		const intervalId = setInterval(() => {
			const current = new Date().getTime();
			const remain = availableTill - current;

			if (remain <= 0) {
				typeof onSuccess == "function" && onSuccess();
				setClock(initState);
				clearInterval(intervalId);
				return;
			}
			const day = padNumber(Math.floor(remain/days));
			let remainder = remain % days;
			const hour = padNumber(Math.floor(remainder / hours));
			remainder = remainder % hours;
			const minute = padNumber(Math.floor(remainder / minutes));
			remainder = remainder % minutes;
			const second = padNumber(Math.floor(remainder / seconds));

			setClock({ day, hour, minute, second });
		}, 1000);
		return () => {
			clearInterval(intervalId);
		};
	}, [availableTill, onSuccess, initState]);

	if (clock.day === "00" && clock.hour === "00" && clock.minute === "00" && clock.second === "00") {
		return <></>;
	}
	return (
		<>
			{`${text || ""} `}
			{!hideDay && `${clock.day}:`}
			{!hideHour && `${clock.hour}:`}
			{!hideMinute && `${clock.minute}:`}
			{clock.second}
			{hideHour && hideMinute && " seconds"}
		</>
	);
};

export default CountDown;
