import { useEffect, useRef } from "react";

export const queryParamsURLEncodedString = (params: {[key: string]: any}) => {
	return Object.keys(params)
		.map((k: string) =>
			Array.isArray(params[k])
				? params[k].map((p: string) => encodeURIComponent(k) + "=" + encodeURIComponent(p)).join("&")
				: params[k] instanceof Object
				? Object.keys(params[k])
						.map((pk) => `${k}[${pk}]=${encodeURIComponent(params[k][pk])}`)
						.join("&")
				: encodeURIComponent(k) + "=" + encodeURIComponent(params[k])
		)
		.join("&");
};

export const useTraceUpdate = (props: Record<string, any>) => {
	const prev = useRef(props);
	useEffect(() => {
		const changedProps = Object.entries(props).reduce((ps: Record<string, any>, [k, v]) => {
			if (prev.current[k] !== v) {
				ps[k] = [prev.current[k], v];
			}
			return ps;
		}, {});
		if (Object.keys(changedProps).length > 0) {
			console.log("Changed props:", changedProps);
		}
		prev.current = props;
	});
};

export const hideEmail = (email: string) => {
	if (!email) return email;
	const length = email.length;
	const head = 3;
	const tail = 4;

	return [email.substr(0, head), "*".repeat(length - head - tail), email.substr(length - tail, length - 1)].join("");
};

export const formatDate = (date: string | number) => {
	const d = new Date(date);
	let month = "" + (d.getMonth() + 1);
	let day = "" + d.getDate();
	let year = d.getFullYear();

	if (month.length < 2) month = "0" + month;
	if (day.length < 2) day = "0" + day;

	return [year, month, day].join("-");
};

// Polyfill for _.merge
const isObject = (item: any) => {
	return item && typeof item === "object" && !Array.isArray(item);
};

export const padNumber = (num: number) => {
	return num < 10 ? "0" + num : num.toString();
};
